
.text-orange {
  color: orange !important;
}
.bg-orange {
  background-color: orange !important;
}
.bg-purple {
  background-color: #dc35a7 !important;
}
#scroll {
    position:fixed;
    right:10px;
    bottom:10px;
    cursor:pointer;
    width:50px;
    height:50px;
    background-color:#3498db;
    text-indent:-9999px;
    display:none;
    border-radius:60px
}
#scroll span {
    position:absolute;
    top:50%;
    left:50%;
    margin-left:-8px;
    margin-top:-12px;
    height:0;
    width:0;
    border:8px solid transparent;
    border-bottom-color:#ffffff;
}
#scroll:hover {
    background-color:#e74c3c;
    opacity:1;filter:"alpha(opacity=100)";
    -ms-filter:"alpha(opacity=100)";
}
.card-bg {
    height: 150px;
    background-size: contain !important;
    background-position: center !important;
    background-repeat: no-repeat !important;
    cursor: pointer;
}
.favorite {
    position: absolute;
    top: 10px;
    right: 10px;
    color: goldenrod;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    font-size: 24px;
}
.favorite:hover {
    cursor: pointer;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
.card {
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}
.card:hover {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.card-title {
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}
.card-title:hover {
    color: #bc781b;
    cursor: pointer;
}






















































































































































































































